

.nav{
    position: fixed;
    top:0;
    padding: 20px;
    width: 100%;
    height: 11px;
    z-index: 1;

    /* animetions */
    transition-timing-function: ease-in;
    transition: all 0.5s;

}

.nav_content{
    display: flex;
    justify-content: space-between;
}

.nav_black{
    background-color: #111 ;
}

.nav_logo{
    position: fixed;
    left: 0;
    top: 1  0px;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
     cursor: pointer;
    }
    
    .nav_avtar{
        position: fixed;
        right: 20px;
        top: 5px;
        width: 30px;
        cursor: pointer;
}
